import { getPlatform } from '../platform/getPlatform.js';
import { getPlatformName } from '../platform/getPlatformName.js';
import { getVersion } from '../platform/getVersion.js';
import { ClientPlatform } from '../platform/types/ClientPlatform.js';
import { MetrikaInitParams } from '../types/MetrikaInitParams.js';

// Если меняете айдишники, то нужно заменить еще и в src/desktop/main/config.ts
const MetrikaCounter = {
    MACOS: '95673848',
    WINDOWS: '95673843',
    WEB: '95673834'
} as const;

const getMetrikaCounter = (): string => {
    if (process.env.BUILD_TYPE === 'desktop') {
        switch (getPlatformName(getPlatform())) {
            case ClientPlatform.MACOS:
                return MetrikaCounter.MACOS;
            case ClientPlatform.WINDOWS:
                return MetrikaCounter.WINDOWS;
        }
    }

    return MetrikaCounter.WEB;
};

export const getMetrikaSettings = (): MetrikaInitParams => {
    const settings: MetrikaInitParams = {
        id: getMetrikaCounter(),
        webvisor: true,
        clickmap: true,
        trackLinks: true,
        accurateTrackBounce: true,
        deffer: true
    };

    if (process.env.BUILD_TYPE === 'desktop') {
        settings.userParams = {
            appVersion: getVersion()
        };
    }

    return settings;
};
